<template>
  <div class="">
    <el-card>
      <div class="tree-container">
        <div style="margin-bottom:10px;">
          <el-input size="small" placeholder="" v-model="filterText"> </el-input>
        </div>
        <el-tree
          ref="tree"
          :data="TemplateList"
          node-key="code"
          default-expand-all
          :expand-on-click-node="false"
          :filter-node-method="filterNode"
          @node-click="treeNodeClick">
          <span class="custom-tree-node" slot-scope="{ node, data }">
            <span>{{ data.name }}</span>
          </span>
        </el-tree>
      </div>

      <div class="detail-container" v-if="nodeName == '' || nodeName == '' || nodeName == '' || nodeName == '' || nodeName == '' || nodeName == ''">
        <div v-if="showDetail">
          <div class="detail-head">
            <span>{{nodeName}}</span>
          </div>
          <el-form ref="form" :model="ScheduleModel" :rules="rules">
            <table class="ClassSettingTableBox">
              <tr>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr v-for="(item, index) in checkedList" :key="item.code">
                <td>{{index + 1}}</td>
                <td>
                  <span v-if="currentScheduleKey !== item.code">{{item.name}}</span>
                  <span v-if="currentScheduleKey === item.code">
                    <el-form-item prop="name">
                      <el-input v-model="ScheduleModel.name" maxlength="50" placeholder="" style="width: 90%"></el-input>
                    </el-form-item>
                  </span>
                </td>
                <td>
                  <div v-if="currentScheduleKey === item.code">
                    <el-button size="mini" type="primary" @click="updateSchedule(item)"></el-button>
                    <el-button size="mini" @click=" currentScheduleKey ='' "></el-button>
                  </div>
                  <div v-if="currentScheduleKey !== item.code">
                    <el-button type="text" icon="el-icon-edit-outline" @click="editSchedule(item)"></el-button>
                    <el-button type="text" icon="el-icon-delete" @click="deleteSchedule(item)"></el-button>
                    <span  v-if="nodeName == ''">
                      <el-button type="text" :disabled="index == 0" icon="el-icon-top" @click="moveSteps(item, index, true)"></el-button>
                      <el-button type="text" :disabled="index == checkedList.length -1" icon="el-icon-bottom" @click="moveSteps(item, index, false)"></el-button>
                    </span>

                  </div>
                </td>
              </tr>

              <tr v-if="isAddRecord">
                <td>{{checkedList.length + 1}}</td>
                <td>
                  <el-form-item prop="name">
                    <el-input v-model="ScheduleModel.name" maxlength="50" placeholder=""style="width: 90%"></el-input>
                  </el-select>
                  </el-form-item>
                </td>
                <td>
                  <div>
                    <el-button size="mini" type="primary"  @click="saveAddSchedule"></el-button>
                    <el-button size="mini" @click=" isAddRecord = false "></el-button>
                  </div>
                </td>
              </tr>
              <tr>
                <td colspan="5">
                  <el-button type="primary" icon="el-icon-plus" @click="addSchedule"></el-button>
                </td>
              </tr>
            </table>
          </el-form>
        </div>
      </div>

      <!--  -->
      <div class="detail-container" v-if="nodeName == ''">
        <div class="detail-head">
          <span>{{nodeName}}</span>
          <el-button size="medium" @click="addInform()"></el-button>
        </div>
        <div class="">
          <el-table :data="informData" style="width: 100%">
            <el-table-column label="" type="index"></el-table-column>
            <el-table-column label="" property="title"></el-table-column>
            <el-table-column label="" property="content"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button size="mini" type="text" @click="handleDetail(scope.row)"></el-button>
                <el-button size="mini" type="text" @click="handleEdit(scope.row)"></el-button>
                <el-button size="mini" type="text" @click="handleDelete(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>


      <!--  -->
      <div class="detail-container" v-if="nodeName == '' ">
        <div v-if="showDetail">
          <div class="detail-head">
            <span>{{nodeName}}</span>
          </div>
          <el-form ref="form" :model="ScheduleModel" :rules="rules">
            <table class="ClassSettingTableBox">
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr v-for="(item, index) in checkedList" :key="item.code">
                <td>{{index + 1}}</td>
                <td>
                  <span v-if="currentScheduleKey !== item.code">{{item.name}}</span>
                  <span v-if="currentScheduleKey === item.code">
                    <el-form-item prop="name">
                      <el-input v-model="ScheduleModel.name" maxlength="50" placeholder="" style="width: 90%"></el-input>
                    </el-form-item>
                  </span>
                </td>
                <td>
                  <span v-if="currentScheduleKey !== item.code">
                    <span v-if="item.step != ''">
                      <el-button type="text" icon="el-icon-add" @click="stepsDetail(item)"></el-button>
                    </span>
                    <span v-if="item.step == ''">
                      <el-button type="text" icon="el-icon-add" @click="treatmentSteps(item)"></el-button>
                    </span>
                  </span>
                  <span v-if="currentScheduleKey === item.code"></span>
                </td>
                <td>
                  <span v-if="currentScheduleKey !== item.code">
                    {{item.status == 1 ? '' : ''}}
                  </span>
                  <span v-if="currentScheduleKey === item.code">
                    <el-switch
                      style="display: block"
                      v-model="ScheduleModel.status"
                      active-value="1"
                      inactive-value="2"
                      active-text=""
                      inactive-text="">
                    </el-switch>
                  </span>
                </td>
                <td>
                  <div v-if="currentScheduleKey === item.code">
                    <el-button size="mini" type="primary" @click="updatePlan(true)"></el-button>
                    <el-button size="mini" @click=" currentScheduleKey ='' "></el-button>
                  </div>
                  <div v-if="currentScheduleKey !== item.code">
                    <el-button type="text" icon="el-icon-edit-outline" @click="editSchedule(item)"></el-button>
                    <el-button type="text" icon="el-icon-delete" @click="deleteSchedule(item)"></el-button>
                  </div>
                </td>
              </tr>

              <tr v-if="isAddRecord">
                <td>{{checkedList.length + 1}}</td>
                <td>
                  <el-form-item prop="name">
                    <el-input v-model="ScheduleModel.name" maxlength="50" placeholder=""style="width: 90%"></el-input>
                  </el-select>
                  </el-form-item>
                </td>
                <td>
                  <span></span>
                </td>
                <td>
                  <el-switch
                    style="display: block"
                    v-model="ScheduleModel.status"
                    active-value="1"
                    inactive-value="2"
                    active-text=""
                    inactive-text="">
                  </el-switch>
                </td>
                <td>
                  <div>
                    <el-button size="mini" type="primary"  @click="saveAddSchedule"></el-button>
                    <el-button size="mini" @click=" isAddRecord = false "></el-button>
                  </div>
                </td>
              </tr>
              <tr>
                <td colspan="5">
                  <el-button type="primary" icon="el-icon-plus" @click="addSchedule"></el-button>
                </td>
              </tr>
            </table>
          </el-form>
        </div>
      </div>

    </el-card>

    <el-dialog :title="dialogTitle" :visible.sync="showInform">
        <el-form :inline="true" label-width="100px" ref="submitForm" :model="submitForm" :rules="submitFormRules" style="margin: 20px 0;">
            <el-form-item label="" prop="title">
              <el-input placeholder="" :disabled="addDisabled" v-model="submitForm.title" style="width: 600px"></el-input>
            </el-form-item>
            <br>
            <el-form-item label="" prop="content">
              <el-input type="textarea" :rows="12" :disabled="addDisabled" placeholder="" v-model="submitForm.content" style="width: 600px"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <span v-if="dialogTitle == ''">
            <el-button type="primary" @click="handelAdd()"></el-button>
          </span>
          <span v-if="dialogTitle == ''">
            <el-button type="primary" @click="handleUpdate()"></el-button>
          </span>
          <el-button @click="showInform = false "></el-button>
        </span>
    </el-dialog>

    <!--  -->
    <el-dialog :title="diffTitle" :visible.sync="showStepsDialog">
      <el-table :data="configStepsList" @selection-change="handleSelectionChange" ref="multipleTable" tooltip-effect="dark" style="width: 100%">
        <span v-if="diffDialog">
          <el-table-column type="selection" width="55"> </el-table-column>
        </span>
        <el-table-column label="" type="index"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
      </el-table>
        <span slot="footer" class="dialog-footer">
          <span v-if="diffDialog">
            <el-button type="primary" @click="updatePlan(false)"></el-button>
          </span>
          <el-button @click="showStepsDialog = false "></el-button>
        </span>
    </el-dialog>

  </div>
</template>


<script>
import storage from '@/tools/local_storage.js'

export default {
    components:{},
    props:{
      TemplateList: {
        type: Array,
        default: ()=>({})
      }
    },
    data() {
      return {
        userInfo:  storage.get('userInfo'),
        templateList: [],
        filterText: '',
        showDetail: false,
        isAddRecord: false,
        ScheduleModel: {
          status: '1',
          name: '',
        },
        rules: {
            name: [
              { required: true, message: '', trigger: 'blur' }
            ]
        },
        currentScheduleKey: "",
        checkedList: [],
        informData: [],
        currentScheduleItem: {},  //
        nodeName: '', //
        showInform: false,
        addDisabled: false,
        dialogTitle: '',
        submitForm: {
          title: '',
          content: '',
        },
        submitFormRules: {
          title: [
            { required: true, message: '', trigger: 'blur' }
          ],
          content: [
            { required: true, message: '', trigger: 'blur' }
          ],
        },
        configStepsList: [],
        showStepsDialog: false,
        diffDialog: true,
        diffTitle: '',
        multipleSelection: [],
        maxSort: '',

      }
    },
    watch: {
        filterText(val) {
          this.$refs.tree.filter(val);
        }
      },
    mounted() {

    },
    methods: {

      //
      addSchedule() {
        this.getMaxSort()
        this.isAddRecord = true;
        this.currentScheduleKey = '';
        this.ScheduleModel.name = "";
      },

      // 
      saveAddSchedule() {
        const { name } = this.ScheduleModel;
        this.$refs['form'].validate((valid) => {
          if (valid) {
            if (this.nodeName == '') {
              this.addDentale(name, '2')
            }
            if (this.nodeName == '') {
              this.addDentale(name, '1')
            }
            if (this.nodeName == '') {
              this.addOrthodonticsPlan(name, '1')
            }
            if (this.nodeName == '') {
              this.addOrthodonticsPlan(name, '2')
            }
            if (this.nodeName == '') {
              this.addOrthodonticsPlan(name, '3')
            }
            if (this.nodeName == '') {
              this.addOrthodonticsPlan(name, '4')
            }
            if (this.nodeName == '') {
              this.addTreatmentPlan(name)
            }
          }else{
            console.log('submit error')
          }
        })
      },

      // 
      editSchedule(item) {
        const { code, name } = item;
        this.isAddRecord = false;
        this.currentScheduleItem = item;
        this.currentScheduleKey = code;
        this.ScheduleModel.name = name;
      },

      // 
      updateSchedule() {
        const { name } = this.ScheduleModel;
        const { code } = this.currentScheduleItem;
        this.$refs['form'].validate((valid) => {
          if (valid) {
            if (this.nodeName == '') {
              this.editDentale(name, '2', code)
            }
            if (this.nodeName == '') {
              this.editDentale(name, '1', code)
            }
            if (this.nodeName == '') {
              this.editOrthodonticsPlan(name, '1', code)
            }
            if (this.nodeName == '') {
              this.editOrthodonticsPlan(name, '2', code)
            }
            if (this.nodeName == '') {
              this.editOrthodonticsPlan(name, '3', code)
            }
            if (this.nodeName == '') {
              this.editOrthodonticsPlan(name, '4', code)
            }

          }else{
            console.log('submit error')
          }
        })
      },

      //
      deleteSchedule(item) {
        this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            if (this.nodeName == '') {
              this.deleteDentale(item.code, '2')
            }
            if (this.nodeName == '') {
              this.deleteDentale(item.code, '1')
            }
            if (this.nodeName == '') {
              this.deleteOrthodonticsPlan(item.code, '1')
            }
            if (this.nodeName == '') {
              this.deleteOrthodonticsPlan(item.code, '2')
            }
            if (this.nodeName == '') {
              this.deleteOrthodonticsPlan(item.code, '3')
            }
            if (this.nodeName == '') {
              this.deleteOrthodonticsPlan(item.code, '4')
            }
            if (this.nodeName == '') {
              this.deletePlan(item.code)
            }
          }).catch(() => {
            this.$message.info('');
          });
      },


      //
      treeNodeClick(data, node, vuecomponent){
        this.nodeName = data.name
        if (data.type == 2) {
          this.showDetail = true
          if (data.name == '') {
             this.getDentaleDetail('2')
          }
          if (data.name == '') {
             this.getDentaleDetail('1')
          }
          if (data.name == '') {
             this.detailOrthodonticsPlan('1')
          }
          if (data.name == '') {
             this.detailOrthodonticsPlan('2')
          }
          if (data.name == '') {
             this.detailOrthodonticsPlan('3')
          }
          if (data.name == '') {
             this.detailOrthodonticsPlan('4')
          }
          if (data.name == '') {
            this.treatmentPlanList()
          }
          if (data.name == '') {
            this.informedconsentList()
          }
        } else {
          this.showDetail = false
        }
      },

      filterNode(value, data) {
          if (!value) return true;
          return data.name.indexOf(value) !== -1;
      },

      //2  1  
      getDentaleDetail (type) {
        this.$http.post('/api/hx/diagnosis/list.do',{
          type
        }).then( res => {
          if (res.data.result) {
            this.checkedList = res.data.returnObject.rows
          } else {
            this.$message.error(res.data.errorMessage)
          }
        })
      },

      //-1  -2
      addDentale(name, type) {
        this.$http.post("/api/hx/diagnosis/add.do", {
          name,
          type
        }).then(res => {
          const { result, errorMessage } = res.data;
          if (result) {
            this.isAddRecord = false;
            this.getDentaleDetail(type);
            this.$message.success( "")
          } else {
            this.$message.error(errorMessage);
          }
        })
      },

      //
      editDentale(name, type, code) {
        this.$http.post("/api/hx/diagnosis/edit.do", {
          name,
          type,
          code
        }).then(res => {
          const { result, errorMessage } = res.data;
          if (result) {
            this.currentScheduleKey = ''
            this.getDentaleDetail(type);
            this.$message.success( "")
          } else {
            this.$message.error(errorMessage);
          }
        })
      },

      //
      deleteDentale(code, type) {
        this.$http.post("/api/hx/diagnosis/del.do", {
          code
        }).then(res => {
          const { result, errorMessage } = res.data;
          if (result) {
            this.isAddRecord = false;
            this.getDentaleDetail(type);
            this.$message.success( "")
          } else {
            this.$message.error(errorMessage);
          }
        })
      },

      //  1 2 3 4
      detailOrthodonticsPlan(type) {
        this.$http.post('/api/hx/orthodonticsPlan/list.do',{
          type
        }).then( res => {
          if (res.data.result) {
            this.checkedList = res.data.returnObject.rows

          } else {
            this.$message.error(res.data.errorMessage)
          }
        })
      },



      //  1 2 3 4
      addOrthodonticsPlan(name, type) {
        // let sort = this.checkedList.length + 1
        let params = {
          name,
          type
        }
        if (type == '4') {
          params.sort = this.maxSort + 1
        }

        this.$http.post("/api/hx/orthodonticsPlan/add.do", params).then(res => {
          const { result, errorMessage } = res.data;
          if (result) {
            this.isAddRecord = false;
            this.detailOrthodonticsPlan(type);
            this.$message.success( "")
          } else {
            this.$message.error(errorMessage);
          }
        })
      },

      //  1 2 3 4
      editOrthodonticsPlan(name, type, code) {
        this.$http.post("/api/hx/orthodonticsPlan/edit.do", {
          name,
          code,
          nextCode: '',
          nextSort: '',
          move: false,
        }).then(res => {
          const { result, errorMessage } = res.data;
          if (result) {
            this.currentScheduleKey = ''
            this.detailOrthodonticsPlan(type);
            this.$message.success( "")
          } else {
            this.$message.error(errorMessage);
          }
        })
      },

      // 1 2 3 4
      deleteOrthodonticsPlan(code, type) {
        this.$http.post("/api/hx/orthodonticsPlan/del.do", {
          code
        }).then(res => {
          const { result, errorMessage } = res.data;
          if (result) {
            this.isAddRecord = false;
            this.detailOrthodonticsPlan(type);
            this.$message.success( "")
          } else {
            this.$message.error(errorMessage);
          }
        })
      },

      //
      moveSteps(item, index, flag) {
        let next
        if (flag) {
          next = this.checkedList[index - 1]
        } else {
          next = this.checkedList[index + 1]
        }

        this.$http.post("/api/hx/orthodonticsPlan/edit.do", {
          // name: item.name,
          code: item.code,
          sort: item.sort,
          nextCode: next.code,
          nextSort: next.sort,
          move: true
        }).then(res => {
          const { result, errorMessage } = res.data;
          if (result) {
            this.detailOrthodonticsPlan('4');
            this.$message.success( "")
          } else {
            this.$message.error(errorMessage);
          }
        })
      },

//-------  -------//
  //
  treatmentSteps(item) {
    const { code, name } = item;
    this.currentScheduleItem = item;
    this.showStepsDialog = true
    this.diffDialog = true
    this.diffTitle = ''

    this.$http.post('/api/hx/orthodonticsPlan/list.do',{
      type: '4'
    }).then( res => {
      if (res.data.result) {
        this.configStepsList = res.data.returnObject.rows
      } else {
        this.$message.error(res.data.errorMessage)
      }
    })
  },

  //
  treatmentPlanList() {
    this.$http.post('/api/hx/treatmentPlan/list.do').then( res => {
      if (res.data.result) {
        this.checkedList = res.data.returnObject.rows
      } else {
        this.$message.error(res.data.errorMessage)
      }
    })
  },

  //
  addTreatmentPlan(name) {
    this.$http.post('/api/hx/treatmentPlan/add.do',{
      name,
      step: '',
    }).then( res => {
      if (res.data.result) {
        this.isAddRecord = false;
        this.treatmentPlanList()
        this.$message.success( "")
      } else {
        this.$message.error(res.data.errorMessage)
      }
    })
  },

  //
  updatePlan(flag) {
    const { name } = this.ScheduleModel;
    const { code, status, step, } = this.currentScheduleItem;
    let params = {
      code,
    }
    if (flag) {
      params.step = step
      params.name = name
      params.status = this.ScheduleModel.status
    } else {
      if (this.multipleSelection.length == 0) {
        this.$message.error('')
        return false
      }
      let arr1 = this.multipleSelection.map(item => item.code)
      params.step = arr1.join(',')
      params.status = status
      params.name = this.currentScheduleItem.name
    }
    // console.log(params)
    this.$http.post('/api/hx/treatmentPlan/edit.do',params).then( res => {
      if (res.data.result) {
        if (!flag) {
          this.showStepsDialog = false
        }
        this.currentScheduleKey = ''
        this.treatmentPlanList()
        this.$message.success( "")
      } else {
        this.$message.error(res.data.errorMessage)
      }
    })
  },

  //
  deletePlan(code) {
    this.$http.post('/api/hx/treatmentPlan/del.do',{
      code
    }).then( res => {
      if (res.data.result) {
        this.treatmentPlanList()
        this.$message.success( "")
      } else {
        this.$message.error(res.data.errorMessage)
      }
    })
  },

  //
  stepsDetail(item) {
    const { code, name } = item;
    this.currentScheduleItem = item;
    this.showStepsDialog = true
    this.diffDialog = false
    this.diffTitle = ''

    this.$http.post('/api/hx/treatmentPlan/get.do',{
      code: item.code,
      step: item.step
    }).then( res => {
      if (res.data.result) {
        this.configStepsList = res.data.returnObject
      } else {
        this.$message.error(res.data.errorMessage)
      }
    })
  },

  //
  handleSelectionChange(val) {
    this.multipleSelection = val;
  },
//-------  -------//


//-------  -------//
  addInform() {
    this.showInform = true
    this.addDisabled = false
    this.dialogTitle = ''
  },

  //
  informedconsentList() {
    this.$http.post('/api/hx/informedconsent/list.do').then( res => {
      if (res.data.result) {
        this.informData = res.data.returnObject.rows
      } else {
        this.$message.error(res.data.errorMessage)
      }
    })
  },

  //
  handelAdd() {
    const { title, content } = this.submitForm
    this.$refs['submitForm'].validate((valid) => {
      if (valid) {
        this.$http.post('/api/hx/informedconsent/add.do',{
          title,
          content
        }).then( res => {
          if (res.data.result) {
            this.showInform = false
            this.informedconsentList()
            this.$message.success( "")
          } else {
            this.$message.error(res.data.errorMessage)
          }
        })
      }
    })
  },

  //
  handleDelete(row) {
    this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http.post("/api/hx/informedconsent/del.do", {
          code: row.code
        }).then(res => {
          const { result, errorMessage } = res.data;
          if (result) {
            this.informedconsentList()
            this.$message.success( "")
          } else {
            this.$message.error(errorMessage);
          }
        })
      }).catch(() => {
        this.$message.info('');
      });

  },

  //
  handleEdit(row) {
    this.showInform = true
    this.addDisabled = false
    this.dialogTitle = ''
    this.submitForm.code = row.code
    this.getDetail(row)
  },

  //
  handleUpdate() {
    const { title, content, code } = this.submitForm
    this.$refs['submitForm'].validate((valid) => {
      if (valid) {
        this.$http.post('/api/hx/informedconsent/edit.do',{
          title,
          content,
          code
        }).then( res => {
          if (res.data.result) {
            this.showInform = false
            this.submitForm = {}
            this.informedconsentList()
            this.$message.success( "")
          } else {
            this.$message.error(res.data.errorMessage)
          }
        })
      }
    })
  },

  //
  handleDetail(row) {
    this.showInform = true
    this.addDisabled = true
    this.dialogTitle = ''
    this.getDetail(row)
  },

  //
  getDetail(row) {
    this.$http.post("/api/hx/informedconsent/get.do", {
      code: row.code
    }).then(res => {
      const { result, errorMessage } = res.data;
      if (result) {
        this.submitForm = res.data.returnObject
      } else {
        this.$message.error(errorMessage);
      }
    })
  },

  //-------  -------//
  //sort 
  getMaxSort() {
    this.$http.post("/api/hx/orthodonticsPlan/getMaxSort.do",).then(res => {
      const { result, errorMessage } = res.data;
      if (result) {
        this.maxSort = res.data.returnObject
      } else {
        this.$message.error(res.data.errorMessage)
      }
    })
  },


    },

}
</script>

<style lang="less" scoped>
    .tree-container{
      width: 28%;
      display: inline-block;
      padding-right: 15px;
    }
    .detail-container{
      width: 69%;
      display: inline-block;
      border-left: 1px solid #f1f1f1;
      vertical-align: top;

      .detail-head{
        width: 100%;
        padding: 0 20px;
        font-size: 16px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;
      }
    }
    .ClassSettingTableBox {
      width: 100%;
      text-align: center;
      tr td {
        border: 1px solid #eeeeee;
        width: 12.5%;
        height: 60px;
        position: relative;
      }
    }


</style>



